<template>
  <div class="uc-material">
    <div class="flex items-center">
      <span
        >您共有 <b class="text-primary">{{ list.length }} </b>条素材</span
      >
      <a-button type="primary" class="v-ml-auto">添加素材</a-button>
    </div>
    <a-divider style="margin: 10px 0" />
    <a-list item-layout="horizontal" :data-source="list">
      <a-list-item slot="renderItem" slot-scope="item, index">
        <a-list-item-meta>
          <span slot="description">{{ item.publishTime }}</span>
          <a slot="title" href="https://www.antdv.com/">
            <a-tag color="#f56a00" v-if="item.materialType === 1">视频</a-tag>
            <a-tag color="#203351" v-else>图片</a-tag>
            <span style="font-size: 16px">{{ item.title }}</span>
          </a>
          <a-avatar :size="48" slot="avatar" icon="picture" shape="square" />
        </a-list-item-meta>
        <a slot="actions">预览</a>
        <a slot="actions">删除</a>
      </a-list-item>
    </a-list>
  </div>
</template>
<script>
export default {
  data() {
    return {
      list: [],
    }
  },
  created() {
    let list = []
    for (let index = 0; index < 15; index++) {
      list.push({
        publishTime: '2021-04-20 13:' + (60 - index),
        materialType: index % 3 === 1 ? 1 : 2,
        title: '使用 eagle 工具设计印制电路板' + index,
        status: 1,
      })
    }
    this.list = list
  },
}
</script>
